<p><span class="rvts17">基础符文合成：</span></p>
<p><span class="rvts21">基础符文的合成比较简单，原材料均可来自大自然相关的元素物品，然而，所有的符文合成都需要有仙粉作为添加剂。在合成巨大号符文时，需要用到</span><span class="rvts20"></span><span class="rvts21">上等的仙粉</span><span class="rvts20"></span><span class="rvts21">。</span>
