<p><span class="rvts21">武器槽：</span><span class="rvts20">+15%</span><span class="rvts21">伤害（转化为物理型）</span>
<br><span class="rvts21">盔甲槽：</span><span class="rvts20">+40%</span><span class="rvts21">物理防御值</span>
<br><span class="rvts21">护身符槽：</span><span class="rvts20">+40%</span><span class="rvts21">魔法防御值</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts18"><br></span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



